.card {
  width: 300px;
  height: 200px;
  background: rgba(0,0,0,0);
  padding: 2rem 1.5rem;
  transition: box-shadow 0.3s ease, transform 0.2s ease;
  padding-top: 20px;
  position: relative;
  z-index: 100;
}

.card-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: transform 0.2s ease, opacity 0.2s ease;
}

/*Image*/
.resumeLinkImage{
  -webkit-filter: drop-shadow(1px 1px 1px #222);
  filter: drop-shadow(1px 1px 1px #222);
}

/*Card footer*/
.card-social {
  transform: translateY(200%);
  display: flex !important;
  justify-content: space-around;
  width: 100%;
  opacity: 0;
  transition: transform 0.2s ease, opacity 0.2s ease;
  left: -8%;
  position: relative;
}

.card-social__item {
  list-style: none;
}

.card-social__item svg {
  display: block;
  height: 25px;
  width: 35px;
  fill: #515f65;
  cursor: pointer;
  transition: fill 0.2s ease, transform 0.2s ease;
}

/*Text*/
.card-title {
  color: #555;
  font-size: 1.5em;
  font-weight: 600;
  line-height: 2rem;
}

.card-avatar {

  transition: transform 0.2s ease;
}

.card-subtitle {
  color: #859ba8;
  font-size: 0.8em;
}
.card-avatar:hover {
  transform: scale(1.1);
 }

/*Hover*/
.card:hover {
  box-shadow: 0 8px 50px #23232333;
}

.card:hover .card-info {
  transform: translateY(-5%);
}

.card:hover .card-social {
  transform: translateY(100%);
  opacity: 1;
}

.card-social__item svg:hover {
  fill: #232323;
  transform: scale(1.1);
}

.pageTwoTransitionOut {
  position: relative;
  top: 100vh;
  z-index: 10;
  fill: rgb(0, 0, 0);
  opacity: 0;
}

.secondPageTitle {
  font-family: Rajdhandi-Regular;
  padding-top: 10px;
  font-size: 10vh;
  width: fit-content;
  position: relative;
  top:0vh;
  text-shadow: 1px 1px 0 white;
}
.prevent-select {
  -webkit-user-select: none; /* Safari */
  -ms-user-select: none; /* IE 10 and IE 11 */
  user-select: none; /* Standard syntax */
}



.secondPage {
  background-color: rgba(0,0,0,0);
  height: 100vh;
  width: 100vw;
  z-index: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  padding-top: -50%;
  z-index: 2;
}


.thirdPage{
  position: relative;
  height: 100vh;
  width: 100vw;
  z-index: 1010;
  top: 0;
  background-color: #222;
  fill: black;
  background: black;
}

.scrollBtn {
  width: 30px;
  height: 50px;
  border-radius: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  border: none;
  outline: 2px solid rgb(135, 105, 255);
  box-shadow: 0px 0px 10px rgb(105, 127, 255);
  position: relative;
  top: 10%;
}

.scroll {
  width: 5px;
  height: 10px;
  border-radius: 10px;
  background-color: rgb(135, 105, 255);
  box-shadow: 0px 0px 10px rgb(135, 105, 255);
  animation: scroll_4013 2s linear infinite;
  transform: translateY(40%);
}

.scrollBtn:after {
  content: 'scroll';
  position: absolute;
  top: 140%;
  color: black;
  text-transform: uppercase;
  letter-spacing: 1.5px;
}

@keyframes scroll_4013 {
  0% {
    transform: translateY(40%);
  }

  50% {
    transform: translateY(90%);
  }
};

